@use "../util" as *;

.navbar {
  overflow: hidden;
  z-index: 1000;
  position: fixed;
  &_menu {
    height: rem(50);
    width: rem(50);
    padding-left: 0.3rem;
    position: relative;
    transition: 0.4s ease;

    &__togglebox {
      position: relative;
      display: block;
      height: rem(50);
      width: rem(50);
      border-radius: rem(50);
      cursor: pointer;

      .logo {
        position: absolute;
        height: rem(4);
        width: rem(35);
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: var(--whiteLight);
        border-radius: rem(50);
        &::before,
        &::after {
          position: absolute;
          content: "";
          height: rem(4);
          width: rem(35);
          left: 50%;
          transform: translate(-50%, -50%);
          background-color: var(--whiteLight);
          border-radius: rem(50);
          transition: 0.3s;
        }
        &::before {
          top: rem(12);
        }
        &::after {
          bottom: rem(8);
        }
      }
    }
    .active {
      background-color: var(--white);
      border-radius: rem(50);
      border: solid rem(3) var(--violet);
      .logo {
        width: 0;
        &::before {
          background-color: var(--violet);
          width: rem(32);
          transform-origin: right;
          transform: translate(-20px, 0px) rotate(45deg);
        }
        &::after {
          background-color: var(--violet);
          width: rem(32);
          transform-origin: right;
          transform: translate(-20px, -2px) rotate(-45deg);
        }
      }
    }
    &__profil {
      text-align: center;
      margin: rem(15) 0 rem(45);
    }
    &__items {
      width: 100%;
      li {
        a {
          color: var(--black);

          .item {
            border-bottom: solid rem(1);
            padding-bottom: rem(11);
          }
          span {
            position: relative;
            display: block;
            // text-align: center;
            letter-spacing: 2;
            padding-left: rem(16);
            margin-top: rem(21);
            width: 100%;
          }
        }
      }
    }
  }
  .block {
    margin: 0;
    height: 100vh;
    width: 100%;
    background-color: var(--white);
  }
}
