  .banner__image.show {
    gap: 0;
    overflow: hidden;
    background: url(https://res.cloudinary.com/digital-x-sarl/image/upload/v1665077205/mariage/banner/bannerimg.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100vh;
  }
  .show > div {
    opacity: 1;
  }

  .banner__playbtn {
    display: none;
  }
  .banner__text {
    display: block;
    position: absolute;
    top: 60%;
    transform: translate(-50%, -50%);
    color: #fff;
    left: 50%;
    font-family: "STIX Two Text";
    font-style: normal;
    text-align: center;

    :nth-child(1) {
      font-weight: 400;
      font-size: rem(28);
      //line-height: rem(35);
      letter-spacing: 0.26em;
      @include breakpoint-down(xsmall) {
        font-size: 1.5rem;
      }
      @include breakpoint-down(mobile) {
        font-size: rem(12);
      }
    }

    :nth-child(2) {
      font-weight: 400;
      font-size: rem(64);
      @include breakpoint-down(xsmall) {
        font-size: 2.4rem;
      }
      @include breakpoint-down(mobile) {
        font-size: 1.8rem;
      }
    }
    :nth-child(3) {
      font-weight: 400;
      font-size: rem(20);
      //line-height: rem(25);
      letter-spacing: 0.26em;
      @include breakpoint-down(xsmall) {
        font-size: 1rem;
      }
      @include breakpoint-down(mobile) {
        font-size: rem(10);
      }
    }
  }
  .banner__text p {
    opacity: 0;
  }
